<script>
let id = window.location.href.toString().split('=')[2];
import axios from 'axios'
import json from '@/static/islamic/1.json'
import autophp from '@/plugins/islamic_auto';
import autophp2 from '@/plugins/autophps';
let sdb = new autophp();
let sdb2 = new autophp2();
import _ from 'lodash'
import firebase from 'firebase'
let db = firebase.firestore();
export default{
    layout:'app_islamic',
    data(){
        return{
            datanya:[],
            bookmarks:[],
            latests:[],
            cordova: false,
            playsemua:false,
            loading2:false,
            showTafsir:false,
            bookmarkmenu:false,
            juzmenu:false,
            showSetting:false,
            caridata:'ayat',
            tafsir:{},
            goto:'',
            cariayat:'',
            carijuz:'1',
            loading:true,
            visible:2,
            lebar:[
                'text-lg',
                'text-xl',
                'text-2xl',
                'text-4xl',
                'text-5xl',
            ],
            tajwid:[{tajwid:'hamza-wasl',color:'#AAAAAA',},{tajwid:'silent',color:'#AAAAAA',},{tajwid:'laam-shamsiyah',color:'#AAAAAA',},{tajwid:'madda-normal',color:'#537FFF',},{tajwid:'madda-permissible',color:'#4050FF',},{tajwid:'madda-necesssary',color:'#000EBC',},{tajwid:'qalaqah',color:'#DD0008',},{tajwid:'madda-obligatory',color:'#2144C1',},{tajwid:'ikhafa-shafawi',color:'#D500B7',},{tajwid:'ikhafa',color:'#9400A8',},{tajwid:'idgham-shafawi',color:'#58B800',},{tajwid:'iqlab',color:'#26BFFD',},{tajwid:'idgham-with-ghunnah',color:'#169777',},{tajwid:'idgham-without-ghunnah',color:'#169200',},{tajwid:'idgham-mutajanisayn',color:'#A1A1A1',},{tajwid:'idgham-mutaqaribayn',color:'#A1A1A1',},{tajwid:'ghunna',color:'#FF7E1E',},],
            pilihanLebar:'text-4xl',
            showTajwid:'tafsir',
            listSurahs:[]
        }
    },
    watch:{
        pilihanLebar(){
            this.showTafsir=false;
        }
    },
    computed:{
        td(){
            let that =this;
            let data =this.datanya;
            return data;
        },
        td2(){
            let that=this;
            let data =this.$store.state.juz;
            data=data.filter(e=>{
              if(e.juz==that.carijuz){
                return e
              }
            })
            return data;
        },
        ayat(){
            let that =this;
            let data =this.datanya.verses;
            data.forEach((e,i)=>{
                e.index=i+1;
            })
            data=data.filter(e=>{
                if(e.number.inSurah.toString().indexOf(this.cariayat)!=-1){
                    return e;
                }
            })
            // data=data.map((e,i)=>{
            //     return{
            //         no:i+1,
            //         ...e
            //     }
            // })
            return data;
        },
    },
    methods: {
        playAllAudio(){
            this.playsemua=!this.playsemua;
            setTimeout(() => {
                document.getElementById('playAllAudio').play();
            }, 3000);
        },
         getListSurah(){
             let that=this;
            axios.get('https://al-quran-8d642.firebaseio.com/data.json?print=pretty').then(res=>{
                this.listSurahs=res.data.filter(e=>{
                    if(e.nomor==that.$route.query.surat){
                        return e
                    }
                });
                console.log('listsurah',this.listSurahs)
                this.$forceUpdate();
            })
        },
        visibilityChanged(data){
            // console.log(data)
             this.visible=this.visible+4;
