async upload(file,compres=false,scrambles=true) {
  let that = this;
  let el = file; // berikan id pada input file
  const options = {
    maxSizeMB: 1,
    maxWidthOrHeight: 1920,
    useWebWorker: true
  }
  try {
    let file = el.files[0];
    let fd = new FormData();
    if(compres){
      await importScripts2('https://cdn.jsdelivr.net/npm/browser-image-compression@1.0.14/dist/browser-image-compression.min.js');
      const compressedFile = await imageCompression(file, options);
      fd.append("file", compressedFile);
    }else{
      fd.append("file", file);
    }
    let setoken = await this.verifyEnc()
    fd.append("api-token", scramble(apitoken));
    fd.append('se-token',setoken);
    if(scrambles){
      fd.append('scramble','true');
    }else{
      fd.append('scramble','false');
    }
    fd.append('data', `select * from tbuser`) //database setting di server
    fd.append('secret', `dsdxxoi4#$(*#sdsaaada@#`) //database setting di server
    if (auth) {
      fd.append('header', localStorage.getItem('auth._token.local'))
      fd.append('key-h',localStorage.getItem('auth._token.local'));
    }
    // return await axios.post(this.urlupload, fd).then(res => {
    return await axios.post(backend!='laravel'?this.urlupload:this.url+'/api/upload/data', fd).then(res => {
      return res.data
    })
  } catch (error) {
    if(popmsg){
      popupMsg('Error Proses Gagal!','bg-red-600')
    }
  }
}
